import { createStore } from "redux";

import { composeWithDevTools } from "redux-devtools-extension";
//import rootReducer from "./rootReducer";

const initialState = {
  // state 초기값
  mainVal: true,
  introVal: true,
};

const mainCheckReducer = (state = initialState, action) => {
  //console.log(state, action);

  switch (action.type) {
    case "PAGE_TYPE":
      return { ...state, mainVal: action.mainVal };
    case "INTRO_TYPE":
      return { ...state, introVal: action.introVal };
    default:
      return state;
  }
};

const store = createStore(mainCheckReducer, composeWithDevTools());
