import React, {useEffect, useState} from 'react';
//import { useDispatch, useSelector } from 'react-redux';
import store from '../app/store';


function Footer() {
	//console.log('ReduxStore footer : '+ store.getState().mainVal);

	function topBtnClick(e){
		e.preventDefault();
		window.scrollTo({top: 0, left: 0, behavior: 'smooth' });
	}
	function floatBtnToggle() {
		const $foot = document.querySelector("#footer");
		if(document.body.contains($foot)){ // 1_3 페이지에서 스크롤 내린 상태에서 다른 페이지 갈때 못찾으면(푸터가 늦게뜨면) getBoundingClientRect 에러나서 추가함.
			const footT = $foot.getBoundingClientRect().top + window.pageYOffset // 푸터 위치
				, scr = window.pageYOffset // 스크롤 현재 위치
				, winH = document.body.clientHeight // 윈도우 높이
				, isFoot = ( footT > scr + winH); // 푸터 위치에 닿으면
			if ( isFoot ) {
				$foot.classList.remove("on");  // 하단에 스크롤 도착시 감춤
			} else {
				$foot.classList.add("on"); // 하단에 스크롤 도착시 노출
			}

			// const	footH = document.querySelector("#footer").offsetHeight;
			
			// console.log(window.innerHeight , footH)
			// document.querySelector("#content").style.height= (window.innerHeight - footH)+"px";
		}
	}
	useEffect(() => {
		//floatBtnToggle();
		if(store.getState().mainVal === true){
			//setDisplay({'display':'none'})

			//console.log('footer sub opened')
		}
		if(store.getState().mainVal === false){
			//setDisplay({'display':'block'})
			//console.log('footer main opened')
		}
		//(store.getState().mainVal === true) ? setDisplay({'display':'none'}) : setDisplay({'display':'block'});
		floatBtnToggle();
		// window.addEventListener("load",floatBtnToggle);
		window.addEventListener("scroll",floatBtnToggle);
		window.addEventListener("resize",floatBtnToggle);
		// return  display;
	},[]);
	return (
		<>
			<footer id="footer" className="main_section fp-auto-height">
				<div className="inwrap">
					<a href="#top" id="fly_btn" className="ibtn_top" onClick={topBtnClick} style={store.getState().mainVal === true ? {'display':'none'} : {'display':'block'}}><span>Top</span></a>
					<ul className="sns">
						<li><a href="#none" target="_blank" className="ibtn_ytb"><span
							className="blind">공식 유튜브 채널 바로가기</span></a></li>
						<li><a href="#none" target="_blank" className="ibtn_fb"><span
							className="blind">공식 페이스북 바로가기 </span></a></li>
						<li><a href="#none" target="_blank" className="ibtn_in"><span
							className="blind">공식 인스타그램 바로가기</span></a></li>
					</ul>

					<div className="family_site">
						<button className="abtn_fam">family site</button>
						<ul>
							<li><a href="http://www.superdeck.co.kr/" rel="noreferrer" target="_blank">SUPERDECK®</a></li>
							<li><a href="http://www.bundrex.com/" rel="noreferrer" target="_blank">BUNDREX®</a></li>
						</ul>
					</div>
					<span className="info">
					<a href="#none" className="btn_privacy">개인정보처리방침</a>
					<a href="#none">이메일 무단 수집 거부</a>
				</span>

					<ul className="address">
						<li>서울시 동대문구 전농동 32-4 하우스토리 4층</li>
						<li>대표 번호 : <a href="tel:02-2106-0129">02-2106-0129</a></li>
						<li>E-mail : <a href="mailto:info@kosteel.co.kr">info@kosteel.co.kr</a></li>
					</ul>
					<p className="copyright">Copyright(C)2021 KOSTEEL CO,.LTD. All Rights Reserved</p>
				</div>
				{/* //inwrap */}
			</footer>
			{/* //footer */}
		</>
	)
};
export default Footer;